* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App {
  margin: 0 auto;
  margin-top: 120px;
  width: 500px;
  height: auto;
  border: 2px solid black;
}
.header {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  text-align: center;
}
.header .header-text {
  font-size: 34px;
  margin-left: 20px;
}
.header .header-button {
  margin-right: 20px;
  width: 65px;
  height: 35px;
  background-color: red;
  border: none;
  border-radius: 5px;
  color: white;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.header .header-button:hover {
  background-color: brown;
}
.header .header-button:active {
  width: 64px;
  height: 34px;
}
.middle-section {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  margin: 20px;
  display: flex;
  flex-direction: column;
}
.middle-section .first-input {
  margin-top: 20px;
}
.middle-section .second-input {
  margin-top: 20px;
}
.middle-section .task-input {
  margin-left: 10px;

  height: 30px;
  width: 90%;
}
.middle-section .date-time-input {
  height: 30px;
  margin-left: 10px;
  width: 90%;
}
label {
  display: block;
}
.reminder {
  padding: 0 10px;
  margin-top: 23px;
  display: flex;
  justify-content: space-between;
}
.middle-section {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.reminder > input {
  width: 20px;
  height: 20px;
  margin-right: 70px;
}
.section-button {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
.middle-button {
  font-size: 14px;
  border: none;
  border-radius: 5px;
  color: white;
  background-color: black;
  margin-right: 30px;
  width: 90%;
  height: 30px;
}
.middle-button:active {
  width: 88%;
}
.main-block {
  border-radius: 5px;
  padding: 20px;

  margin: 0 30px;
  background-color: rgb(214, 214, 214);
  margin-bottom: 15px;
}
.main-reminder {
  border-left-style: solid;
  border-width: 3px;
  border-left-color: green;
}
.top-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.circle-icon {
  width: 30px;
  height: 30px;
  color: red;
  cursor: pointer;
}
.circle-icon:hover {
  color: black;
}
